import { Fragment, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import { selectUser } from '../../../../redux/userReducer';
import { QRCodeSVG } from 'qrcode.react';
import { paymentedOrder, selectWaitingPaymentID } from '../../../../redux/orderReducer';
import { ordersService } from '../../../../services/orders.service';
import { COIN_TYPES, API_BASE } from '../../../../config/constants';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClose } from '@fortawesome/free-solid-svg-icons';

const WaitingPaymentModal = ({isModal, setIsModal, cposOrdered}) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [name, setName] = useState('');  
  const user = useSelector(selectUser);
  const paymentID = useSelector(selectWaitingPaymentID);

  const handlePaymentCompleted = () => {   
    dispatch(paymentedOrder(paymentID));
  }

  const getPaymentStatus = async (id) => {
    while(true) {
      const ret = await ordersService.paystatusOrder(id);
      if (ret.data.payment) {
        setIsModal(false);
        break;
      }
    }
  }

  useEffect (() => {
    if (paymentID != 0) {
      getPaymentStatus(paymentID)
    }
  }, [paymentID])

  return (
    <Transition appear show={isModal} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full">
              <div className="relative bg-[#404040] w-[30vw] max-w-[1000px] min-w-[380px] min-h-[160px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}  
                <div className='flex justify-between items-center'>
                  <h1 className='text-md text-left text-[#D9D9D9] select-none'>Waiting for payment:</h1>
                  <FontAwesomeIcon icon={faClose} className='cursor-pointer' onClick={() => setIsModal(false)}/>
                </div>  
                {/* CONTENTS */}
                <div className="min-h-[2rem] py-[1rem] flex justify-center mb-[1rem]">
                  <QRCodeSVG 
                      value={user ? user.walpublic : '0x0000000000'}
                      level="H"
                      size={256}
                  />                  
                </div>
                {/* CONTROLS */}
                <div className='absolute right-6 bottom-4'>
                  {cposOrdered.coinAmount && <h1 className='text-md text-left text-[#D9D9D9] select-none'>Amount: {cposOrdered.coinAmount.toFixed(8)} {COIN_TYPES[cposOrdered.coinType]}</h1>}
                </div>  
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default WaitingPaymentModal;